
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Card from 'vux/src/components/card/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {httpUrl} from '../../http_url';

export default {
directives: {
TransferDom
},
data () {
return {
menus: {
menu1: '退出登录'
},
showMenus: false,
pdfurls:'',
isIOS:true,
transfinite:''      
}
},
created(){

if(this.checkIOS()){
this.isIOS = true;
}else {
this.isIOS = false;
}
},
components: {
XHeader,
PopupHeader,
Actionsheet,
XButton
},
methods:{
goSignature(){
let step_type = this.$route.query.step_type;
let orderid = this.$route.query.orderid;
this.$router.push({
path:'/signature',
query: {
step_type: step_type,
orderid:orderid
}
});
},
goLiveFace(){
const _this = this;
let step_type = this.$route.query.step_type;
let orderid = this.$route.query.orderid;

if(this.transfinite == 1){
this.$vux.alert.show({
title: '错误提示',
content: '失败次数过多，请联系客服：0755-22211333',
onHide () {
_this.$router.push('/home');
}
})
}else {
this.$router.push({
path:'/live_face',
query: {
step_type: step_type,
orderid:orderid
}
});
}

},
getData(){
let orderid = this.$route.query.orderid;
const _this = this;
let postData = this.$qs.stringify({
orderid:orderid
})
this.$axios({
method: 'post',
url:httpUrl.orderInfo,
data:postData
}).then((res)=>{
console.log(res.data)
let data =res.data.retData;
if(res.data.errCode==200){
this.pdfurls = data.orderInfo.ht_url;
this.transfinite = data.transfinite;
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
openHt(){
window.open(this.pdfurls);
},
LoginOut(){
this.$axios({
method: 'post',
url:httpUrl.loginOut,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$router.push('/quick_login');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}

}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
goHome(){
this.$router.push('/home');
}
}
}



